body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
}
.button {
  margin: auto;
  text-align: center;
  display: block;
  font-size: 22px;
}
.box.column.is-3 {
  width: 30%;
  margin: auto;
  text-align: center;
  -webkit-transition: 250ms;
  -o-transition: 250ms;
  transition: 250ms;
}
.box.column.is-3:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}
.title {
  text-align: center;
}
.info-container {
  margin: 13px auto 13px auto;
  text-align: center;
  width: 80%;
}
.footer {
  margin-top: 25px;
}
.container-name-and-pokemon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 30vh;
}
.input-container,.search-container{
  margin: 25px auto 25px auto;
}
.counter {
  font-size: 30px;
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 999;
}

@media(max-width: 800px) {
  .box {
    width: 90%;
  }
}



